import React from 'react'
import { Button } from 'react-bootstrap'
import { Producto } from '../../../Modelos/Productos'
interface PropsFila {
    producto: Producto,
    add: (iten: Producto) => void
    color?: string
}
export default class FilaSeleccion extends React.Component<PropsFila>{
    render() {
        let { producto: { codigo, descripcion } } = this.props
        return <tr>
            <th>{codigo}</th>
            <th>{descripcion}</th>
            <th>
                <Button data-tip="Agregar Producto" onClick={() => { this.props.add(this.props.producto) }} bsStyle="success">
                    <i className="glyphicon glyphicon-plus" />
                </Button>
            </th>
        </tr >
    }

}