import React from 'react';
import { Button, ButtonGroup, Modal, Row } from 'react-bootstrap';
interface Props {
    open: boolean,
    close: () => void,
    title: string
    msn: string
    accion: () => void
}
export default class ModalAlert extends React.Component<Props> {

    render() {
        let { title, close, msn, open, accion } = this.props;
        return <Modal show={open} onHide={close}>
            <Modal.Header style={{backgroundColor:"#f7e1b5"}}>
                <h4 className="m-0 font-weight-bold text-primary mr-auto align-self-center">
                    <span><b>{title}</b></span>
                </h4>
            </Modal.Header>
            <Modal.Body style={{backgroundColor:"#faf2cc"}}>
                <Row style={{padding:"10px 10px 10px 10px"}}>
                    {msn}
                </Row>
            </Modal.Body>
            <Modal.Footer  style={{backgroundColor:"#f7e1b5"}}>
                <ButtonGroup className="pull-right">
                    <Button onClick={accion} bsStyle="success">
                        <span className="glyphicon glyphicon-ok-circle"></span> Aceptar
                    </Button>
                    <Button onClick={close} bsStyle="danger">
                        <span className="glyphicon glyphicon-remove-circle"></span> Cancelar
                    </Button>
                </ButtonGroup>
            </Modal.Footer>
        </Modal>
    }
}