import React from 'react';
import { Button, ButtonGroup, Modal, Row } from 'react-bootstrap';
import SetText from '../../Utils/setTex';
import { post, zfill } from '../../Utils/Utiles';
interface Props {
    open: boolean,
    close: () => void
    id: number
    update?: () => void
}
interface State {
    cod_doc: string
    num_con: string
    fecha_doc: string
}
export default class RegistrarFactura extends React.Component<Props, State> {
    constructor(p: Props) {
        super(p);
        this.state = { cod_doc: "", num_con: "", fecha_doc: "" };
        this.setState = this.setState.bind(this);
    }
    render() {
        let { props: { close, open, id }, state: { cod_doc, num_con, fecha_doc }, setState } = this;
        return <Modal bsSize="lg" show={open} onHide={close}>
            <Modal.Header>
                <h4 className="m-0 font-weight-bold text-primary mr-auto align-self-center">
                    <span><b>REGISTRAR FACTURA DE COMPRA N°: {zfill(id)}</b></span>
                </h4>
            </Modal.Header>
            <Modal.Body>
                <Row style={{ padding: "10px 10px 10px 10px" }}>
                    <div className="form-group col-sm-12">
                        <div className="col-sm-4">
                            <SetText placeholder="Codigo de Documento" setData={setState} update="cod_doc" value={cod_doc} />
                        </div>
                        <div className="col-sm-4">
                            <SetText placeholder="Numero de Comtrol" setData={setState} update="num_con" value={num_con} />
                        </div>
                        <div className="col-sm-4">
                            <SetText placeholder="Fecha de Documento" type="date" setData={setState} update="fecha_doc" value={fecha_doc} />
                        </div>
                    </div>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <ButtonGroup className="pull-right">
                    <Button onClick={this.actualizar.bind(this)} bsStyle="success">
                        <span className="glyphicon glyphicon-ok-circle"></span> Aceptar
                    </Button>
                    <Button onClick={close} bsStyle="danger">
                        <span className="glyphicon glyphicon-remove-circle"></span> Cancelar
                    </Button>
                </ButtonGroup>
            </Modal.Footer>
        </Modal >
    }
    actualizar() {
        post("/api/Compra/facturar/" + this.props.id, this.state, data => { if (this.props.update) this.props.update() });
    }
}