import React from 'react';
import FilaCotizacion from '../../Utils/FilaCotizacionOrden'
import Tabla from '../../Utils/Table';
import { GeneralFila } from '../../Modelos/General';
import { filaLabel } from '../../Utils/Utiles';
export default class ListNotas extends Tabla {
    placeholder = "BUSCAR NOTA DE ENTREGA";
    estatus = [{ value: 0, label: 'CANCELADO' },{ value: 1, label: 'ENTREGADA'}, { value: 2, label: 'FACTURADA' }];
    buscarLabel: filaLabel[] = ["codigo", "nombre", 'fecha', 'nota', 'telefono'];
    title() {
        return <>
            <div className="btn-group pull-right">
                <a href="nueva_cotizacion" className="btn btn-info"><span className="glyphicon glyphicon-plus"></span>NUEVA NOTA DE ENTREGA</a>
            </div>
            <h4><i className="glyphicon glyphicon-search"></i> BUSCAR NOTA DE ENTREGA</h4>
        </>
    }
    Row(p: GeneralFila) {
        return <FilaCotizacion update={this.set.bind(this)} key={"cot" + p.codigo + p.status} data={p} type='Nota' />
    }
    set() {
        fetch('/api/Nota/lista')
            .then(response => response.json())
            .then(data => {
                this.setItens(data);
            });
    }
}
