import React from 'react';
import { Modal, Row } from 'react-bootstrap';
interface Props {
    open: boolean,
    close: () => void,
    url: string,
    title: string
}
export default class ModalPrint extends React.Component<Props> {

    render() {
        let { title, close, url, open } = this.props;
        return <Modal show={open} onHide={close} bsSize="lg">
            <Modal.Header>
                <h6 className="m-0 font-weight-bold text-primary mr-auto align-self-center">
                    <span><b>{title}</b></span>
                </h6>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <iframe src={url} width='100%' height='600wh' title="print" scrolling='no' frameBorder='0'></iframe>
                </Row>
            </Modal.Body>
        </Modal>
    }
}