import React from 'react';
import { Producto, productoLabel } from '../../../Modelos/Productos';
import ModalBusqueda from '../../../Utils/ModalBusqueda';
import FilaSeleccion from './FilaSeleccion';
interface Props {
    type: 'Entrada' | 'salida' | 'Seleccion'
    add: (p: Producto) => void
}
export default class BuscarProducto extends ModalBusqueda<Props, Producto, productoLabel>{
    buscarLabel: productoLabel[]=['codigo','descripcion'];
    
    title() {
        return <h4>BUSCAR PRODUCTOS</h4>
    }
    Header() {
        return <tr>
            <th onClick={() => { this.setOrden("codigo") }}>CODIGO {this.flechas("codigo")}</th>
            <th onClick={() => { this.setOrden("descripcion") }}>DESCRIPCION {this.flechas("descripcion")}</th>
            <th >ACCIONES</th>
        </tr>
    }
    Row(c: Producto) {
        switch (this.props.type) {
            case 'Seleccion': return <FilaSeleccion producto={c} add={this.props.add} />
        }
        return <></>;
    }
    componentDidMount() {
        this.set();
    }
    set() {
        fetch('/api/Producto/lista')
            .then(response => response.json())
            .then(data => {
                this.setItens(data);
                this.forceUpdate();
            });
    }
}