
import React from 'react';
import { Button, ButtonGroup } from 'react-bootstrap';
import ReactTooltip from 'react-tooltip';
import { GeneralFila } from '../Modelos/General';
import ModalPrint from './ModalPrint';
import ModalAlert from './ModalAlert';
import ModalDetalle from './ModalDetalle';
import { formatoFecha, numberFormat, zfill } from './Utiles';
import Seguimientos from './Seguimiento';
import RegistrarFactura from '../App/Compra/RegistrarFactura';

interface Props {
    type: 'Cotizacion' | 'Orden' | 'Nota' | 'Factura' | 'Compra'
    data: GeneralFila
    update?: () => void
}
interface State {
    print: boolean
    detalles: boolean
    aux: boolean
    eliminar: boolean
}
export default class FilaCotizacionOrden extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p);
        this.state = { print: false, detalles: false, aux: false, eliminar: false };
    }
    render() {
        let cot = this.props.data, { type } = this.props,
            maxWidth = window.innerWidth - 890;
        return <tr >
            <th style={{ minWidth: "100px", maxWidth: "100px" }}>
                <ModalDetalle tipo={type} titulo={"DETALLES DE LA " + this.setText() + " N° " + zfill(cot.codigo)} id={cot.codigo} open={this.state.detalles} close={this.close.bind(this)} />
                <Button data-tip="VER DETALLEA" onClick={() => { this.setState({ detalles: true }) }} bsStyle="link" >
                    {zfill(cot.codigo)}
                </Button>
            </th>
            <th style={{ minWidth: "90px", maxWidth: "90px", paddingTop: 10 }}>{formatoFecha(cot.fecha)}</th>
            <th>
                <ReactTooltip />
                <Button
                    style={{ maxWidth, whiteSpace: "nowrap", textOverflow: "ellipsis", overflow: "hidden" }}
                    data-class="toolbody" data-html="true" data-tip={this.tooltipCliente()} bsStyle="link" >
                    {cot.nombre}
                </Button>
            </th>
            <th style={{ minWidth: "160px", maxWidth: "160px", paddingTop: 10 }}>{numberFormat(cot.monto)}</th>
            <th style={{ minWidth: "100px", maxWidth: "100px", paddingTop: 5, fontSize: 17 }}>{this.getStatus()}</th>
            <th style={{ minWidth: "135px", maxWidth: "135px" }}>
                <ModalPrint title={"IMPRIMIR " + this.setText() + " N° " + zfill(cot.codigo)}
                    url={"/api/" + type + "/PDF/" + cot.codigo}
                    open={this.state.print} close={this.close.bind(this)} />
                <ModalAlert title={"ELIMINAR " + this.setText() + " N° " + zfill(cot.codigo)}
                    msn={"ESTA SEGURO QUE ELIMINARA LA " + this.setText() + " N°" + cot.codigo + "?"}
                    open={this.state.eliminar} close={this.close.bind(this)} accion={this.cancelar.bind(this)} />
                {(type === 'Orden' || type === 'Cotizacion') ?
                    <Seguimientos type={type} id={cot.codigo} close={this.close.bind(this)} open={this.state.aux} />
                    : <></>}
                {(type === 'Compra') ?
                    <RegistrarFactura update={this.props.update} id={cot.codigo} close={this.close.bind(this)} open={this.state.aux} />
                    : <></>}
                {this.getButtons()}
            </th>
        </tr >
    }
    getStatus() {
        switch (this.props.data.status) {
            case 0:
                return <span className="label label-danger">CANCELADA</span>
            case 1:
                return <span className="label label-warning">{this.getStatus1()}</span>
            case 2:
                return <span className="label label-success">{this.getStatus2()}</span>
        }
    }
    getStatus1() {
        let { type } = this.props;
        switch (type) {
            case 'Orden': return 'EN ESPERA';
            case 'Compra': return 'SIN FACTURA';
            case 'Cotizacion': return 'PENDIENTE';
            case 'Nota': return 'ENTREGADO';
        }
    }
    getStatus2() {
        let { type } = this.props;
        switch (type) {
            case 'Orden': return 'PROCESADA';
            case 'Compra': return 'FACTURADA';
            case 'Cotizacion': return 'PROCESADA';
            case 'Nota': return 'FACTURADA';
            case 'Factura': return 'PROCESADA';
        }
    }
    getButtons() {
        let buttons = [];
        let { type } = this.props;
        if (type === 'Orden' || type === 'Cotizacion')
            buttons.push(<Button data-tip="VER SEGUIMIENTO" onClick={() => { this.setState({ aux: true }) }} ><i className="glyphicon glyphicon-eye-open"></i></Button>);
        if (type === 'Compra' && this.props.data.status === 1)
            buttons.push(<Button data-tip="REGISTRAR FACTURA" onClick={() => { this.setState({ aux: true }) }} ><i className="glyphicon glyphicon-edit"></i></Button>);
        buttons.push(<Button data-tip="VER IMPRECION" onClick={() => { this.setState({ print: true }) }} bsStyle="primary"><i className="glyphicon glyphicon-download"></i></Button>);
        if (this.eliminable())
            buttons.push(<Button data-tip="CANCELAR" onClick={() => { this.setState({ eliminar: true }) }} bsStyle="danger"><i className="glyphicon glyphicon-ban-circle"></i></Button>);
        return <ButtonGroup className="pull-right">{buttons}</ButtonGroup>
    }
    eliminable() {
        let { data: { status }, type } = this.props;
        if (type === 'Nota' || type === 'Cotizacion')
            return (status === 1)
        if (type === 'Factura')
            return (status === 2)
        if (type === 'Orden' || type === 'Compra')
            return (status > 0)
    }
    setText() {
        let { type } = this.props;
        switch (type) {
            case 'Cotizacion': return 'COTIZACIÓN';
            case 'Compra': return 'COMPRA';
            case 'Factura': return 'FACTURA';
            case 'Nota': return 'NOTA DE ENTREGA';
            case 'Orden': return 'ORDEN DE COMPRA';
        }
    }
    tooltipCliente() {
        let cot = this.props.data
        return "<h9 style='color: #5bc0de'>DATOS DE CONTACTO</h9><br />" +
            "<b>PERSONA DE CONTACTO:</b><br />" +
            cot.contacto + "<br />" +
            "<b>CORREO ELECTRONICO:</b><br />" +
            cot.correo + "<br />" +
            "<b>TELEFONO:</b><br />" +
            cot.telefono
    }
    close() {
        this.setState({ print: false, detalles: false, aux: false, eliminar: false });
    }
    cancelar() {
        fetch("/api/" + this.props.type + "/cancelar/" + this.props.data.codigo)
            .then(response => response.json())
            .then(data => {
                if (data === 1)
                    if (this.props.update)
                        this.props.update()
                this.close();
            });
    }
}