import React from 'react';
import FilaCotizacion from '../../Utils/FilaCotizacionOrden'
import Tabla from '../../Utils/Table';
import { GeneralFila } from '../../Modelos/General';
import { filaLabel } from '../../Utils/Utiles';
export default class ListOrdenes extends Tabla {
    placeholder = "BUSCAR ORDEN DE COMPRA";
    estatus = [{ value: 0, label: 'CANCELADO' }, { value: 1, label: 'EN ESPERA' }, { value: 2, label: 'PROCESADA' }];
    buscarLabel: filaLabel[] = ["codigo", "nombre", 'fecha', 'nota', 'telefono'];
    title() {
        return <>
            <div className="btn-group pull-right">
                <a href="nueva_orden" className="btn btn-info"><span className="glyphicon glyphicon-plus"></span> NUEVA ORDEN DE COMPRA</a>
            </div>
            <h4><i className="glyphicon glyphicon-search"></i> BUSCAR ORDEN DE COMPRA</h4>
        </>
    }
    Row(p: GeneralFila) {
        return <FilaCotizacion update={this.set.bind(this)} key={"cot" + p.codigo + p.status} data={p} type='Orden' />
    }
    Header() {
        let maxWidth = window.innerWidth - 890;
        return <tr className="info">
            <th style={{ minWidth: "100px", maxWidth: "100px", backgroundColor: this.colorOrden("codigo") }} onClick={() => { this.setOrden("codigo") }}>NUMERO {this.flechas("codigo")}</th>
            <th style={{ minWidth: "90px", maxWidth: "90px", backgroundColor: this.colorOrden("fecha") }} onClick={() => { this.setOrden("fecha") }}>FECHA {this.flechas("fecha")}</th>
            <th style={{ minWidth: "100px", maxWidth, backgroundColor: this.colorOrden("nombre") }}
                onClick={() => { this.setOrden("nombre") }}>PROVEEDOR {this.flechas("nombre")}</th>
            <th style={{ minWidth: "160px", maxWidth: "160px", backgroundColor: this.colorOrden("monto") }} onClick={() => { this.setOrden("monto") }}>MONTO {this.flechas("monto")}</th>
            <th style={{ minWidth: "100px", maxWidth: "100px" }}>ESTATUS</th>
            <th style={{ minWidth: "135px", maxWidth: "135px" }}>ACCIONES</th>
        </tr>
    }
    set() {
        fetch('/api/Orden/lista')
            .then(response => response.json())
            .then(data => {
                this.setItens(data);
            });
    }
}
