import React from "react";
import { Button, Col, Pagination, Panel, Row, Table } from "react-bootstrap";
import ReactTooltip from "react-tooltip";
import BuscarProducto from "../App/Productos/BuscarProducto";
import UsuarioSelector from "../App/Usuarios/Select";
import { GeneralFila } from "../Modelos/General";
import { Producto } from "../Modelos/Productos";
import { filaLabel, busqueda, busquedaFechas, ordenarPor, filtrarProducto, filtro } from "./Utiles";
interface State {
    itens: GeneralFila[] | "carro"
    productos: Producto[]
    modalProductos: boolean
    buscar: string
    fecha_ini: string
    fecha_fin: string
    user: number
    estatus: number
    page: number
    open: boolean
    ordenar: filaLabel
}
export default class Tabla extends React.Component<any, State>{
    placeholder = "";
    mostrar = 10
    buscarLabel: filaLabel[] = ['codigo', 'fecha', 'nombre']
    estatus: { label: string, value: number }[] = []
    orden: string = "desc";
    length = 0;
    constructor(p: any) {
        super(p)
        this.setState = this.setState.bind(this);
        this.setBuscar = this.setBuscar.bind(this);
        this.state = { itens: "carro", productos: [], modalProductos: false, buscar: '', fecha_ini: 'dd/mm/aaaa', fecha_fin: 'dd/mm/aaaa', user: -1, estatus: -1, page: 1, open: false, ordenar: 'codigo' };
    }
    close() {
        this.setState({ open: false })
    }
    body() {
        var page = this.state.page - 1;
        var init = page * this.mostrar;
        var fin = init + this.mostrar;
        var itens = this.filtros().filter((iten, index) => {
            if (index >= init && index < fin) {
                return true
            }
            return false
        })
        var pts = itens.map((iten) => {
            return this.Row(iten)
        })
        return pts
    }
    paginacion() {
        if (this.length === 0)
            return <></>
        var total_page = Math.ceil(this.length / this.mostrar)
        var page = this.state.page
        var buttons = []
        if (page === 1) {
            buttons.push(<Pagination.Item key="active1" active>1</Pagination.Item>)
        } else {
            buttons.push(<Pagination.Item key="p1" onClick={this.setpage.bind(this, 1)}>1</Pagination.Item>)
        }
        for (var i = 3; i > 0; i--) {
            if ((page - i) > 1) {
                if (i === 3) {
                    buttons.push(<Pagination.Ellipsis key="Ellipsis1" />)
                } else {
                    buttons.push(<Pagination.Item key={"p" + (page - i)} onClick={this.setpage.bind(this, page - i)}>{page - i}</Pagination.Item>)
                }
            }
        }
        if (total_page > page && page > 1) {
            buttons.push(<Pagination.Item active>{page}</Pagination.Item>)
        }
        for (i = 1; i < 4; i++) {
            if ((page + i) < total_page) {
                if (i === 3) {
                    buttons.push(<Pagination.Ellipsis key="Ellipsis2" />)
                } else {
                    buttons.push(<Pagination.Item key={"p" + (page + i)} onClick={this.setpage.bind(this, page + i)}>{page + i}</Pagination.Item>)
                }
            }
        }
        if (page === total_page) {
            if (1 !== total_page) {
                buttons.push(<Pagination.Item key={"active" + total_page} active>{total_page}</Pagination.Item>)
            }
        } else {
            buttons.push(<Pagination.Item key={"p" + total_page} onClick={this.setpage.bind(this, total_page)}>{total_page}</Pagination.Item>)
        }
        return buttons
    }
    pagination() {
        return <Pagination style={{ position: "relative", float: "right" }}>{this.paginacion()}</Pagination>
    }
    setBuscar(e: React.KeyboardEvent<HTMLInputElement>) {
        let { value } = e.currentTarget
        this.setState({ buscar: value.toUpperCase() });
        this.setpage(1)
    }
    setOrden(label: filaLabel) {
        if (this.state.ordenar === label) {
            if (this.orden === "asc")
                this.orden = "desc";
            else
                this.orden = "asc";
            this.forceUpdate();
        } else {
            this.orden = "desc";
            this.setState({ ordenar: label })
        }
    }
    flechas(label: filaLabel) {
        if (this.state.ordenar === label) {
            if (this.orden === "desc") {
                return <i className="fa fa-arrow-up" />
            } else {
                return <i className="fa fa-arrow-down" />
            }
        }
        return <></>
    }
    colorOrden(label: filaLabel) {
        if (this.state.ordenar === label) {
            return '#afd9ee'
        }
        return ''
    }
    filtros() {
        let { itens, user, fecha_fin, fecha_ini, buscar, productos, estatus, ordenar } = this.state;
        if (itens !== 'carro') {
            itens = busqueda(itens, buscar, this.buscarLabel);
            itens = busquedaFechas(itens, fecha_ini, fecha_fin);
            itens = filtro(itens, user, 'usuario');
            itens = filtro(itens, estatus, 'status');
            itens = filtrarProducto(itens, productos);
            itens = ordenarPor(itens, this.orden, ordenar);
            this.length = itens.length;
            return itens;
        }
        return [];
    }
    render() {
        return <Panel bsStyle="info" id="tabla" expanded={this.state.open}>
            <Panel.Heading>
                {this.title()}
            </Panel.Heading>
            <Panel.Body>
                <Panel.Body style={{ paddingTop: 0 }}>{this.renderBusqueda()}</Panel.Body>
                <Panel.Collapse>
                    <Panel><Panel.Body>{this.Avansado()}</Panel.Body></Panel>
                </Panel.Collapse>
                <Col md={12}>
                    <Table striped condensed hover>
                        <thead>
                            {this.Header()}
                        </thead>
                        <tbody>
                            {this.body()}
                        </tbody>
                    </Table>
                    {this.pagination()}
                </Col>
            </Panel.Body>
        </Panel>
    }
    title() {
        return <></>;
    }
    renderBusqueda() {
        return <Row>
            <ReactTooltip />
            <div className="input-group col-md-12">
                <input style={{ position: 'relative', width: "50%" }} onKeyUp={this.setBuscar.bind(this)} name="buscar" placeholder={this.placeholder} className="form-control" />
                <div className="input-group-append">
                    <Button style={{ width: '100px', position: 'relative', borderRadius: 0 }} bsStyle="primary">
                        <span className="glyphicon glyphicon-search"></span> Buscar
                    </Button>
                    <Button style={{ width: '100px', position: 'relative', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} data-tip="BUSQUEDA AVANSADA" onClick={() => { this.setState({ open: !this.state.open }) }}>
                        <span className="glyphicon glyphicon-wrench"></span> Avanzado
                    </Button>
                </div>
            </div>
        </Row>
    }
    Avansado() {
        return <Row className="form">
            <BuscarProducto type='Seleccion' open={this.state.modalProductos} add={this.addProducto.bind(this)} close={() => { this.setState({ modalProductos: false }) }} />
            <Col style={{ width: '525px', paddingLeft: 10 }} md={6}>
                <div className="input-group">
                    <span style={{ width: '140px', position: 'relative', backgroundColor: "#eee" }} className="form-control">Rango de fechas</span>
                    <input style={{ width: '160px', position: 'relative' }} className="form-control" onChange={this.setFechaInicio.bind(this)} value={this.state.fecha_ini} name="fecha_ini" type="date" />
                    <input style={{ width: '160px', position: 'relative' }} className="form-control" onChange={this.setFechaFin.bind(this)} value={this.state.fecha_fin} name="fecha_fin" type="date" />
                    <Button onClick={this.limpiarFechas.bind(this)} data-tip="LIMPIAR FECHAS" style={{ width: '40px', position: 'relative', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} bsStyle="danger"><i className="glyphicon glyphicon-ban-circle"></i></Button>
                </div>
            </Col>
            <Col style={{ width: '230px', paddingLeft: 0 }} md={2}>
                <select data-tip="FILTRAR POR EL ESTADO" onChange={this.setStatus.bind(this)} className="form-control" defaultValue={this.props.defaultValue}>
                    <option value={-1}>TODOS LOS ESTADOS</option>
                    {this.estatus.map(m => {
                        return <option value={m.value}>{m.label}</option>
                    })}
                </select>
            </Col>
            <Col data-tip="FILTRAR POR USUARIO QUE REALIZO LA OPERACION" style={{ width: '230px', paddingLeft: 0 }} md={3}>
                <UsuarioSelector change={(u: number) => { this.setState({ user: u }) }} defaultValue={this.state.user} />
            </Col>
            <Col md={2}>
                <Button data-tip="FILTRAR POR LOS PRODUCTOS SELECCIONADOS" onClick={() => { this.setState({ modalProductos: true }) }}><span className="glyphicon glyphicon-search"></span>Articulos a Buscar</Button>
            </Col>
            <Col md={12}>
                <Table striped condensed hover>
                    <thead>
                        <tr>
                            <th style={{ width: "10%" }} className="text-center">CODIGO</th>
                            <th style={{ width: "80%" }} >DESCRIPCION</th>
                            <th style={{ width: "10%" }} >Quitar</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.state.productos.map((iten) => {
                            return (<tr>
                                <td className="text-center">{iten.codigo}</td>
                                <td>{iten.descripcion}</td>
                                <td className="text-center">
                                    <Button bsStyle="danger" onClick={() => { this.removeProducto(iten.codigo) }}>
                                        <i className="glyphicon glyphicon-ban-circle"></i>
                                    </Button>
                                </td>
                            </tr>);
                        })}
                    </tbody>
                </Table>
            </Col>
        </Row >
    }
    Header() {
        let maxWidth = window.innerWidth - 890;
        return <tr className="info">
            <th style={{ minWidth: "100px", maxWidth: "100px", backgroundColor: this.colorOrden("codigo") }} onClick={() => { this.setOrden("codigo") }}>NUMERO {this.flechas("codigo")}</th>
            <th style={{ minWidth: "90px", maxWidth: "90px", backgroundColor: this.colorOrden("fecha") }} onClick={() => { this.setOrden("fecha") }}>FECHA {this.flechas("fecha")}</th>
            <th style={{ minWidth: "100px", maxWidth, backgroundColor: this.colorOrden("nombre") }} onClick={() => { this.setOrden("nombre") }}>CLIENTE {this.flechas("nombre")}</th>
            <th style={{ minWidth: "160px", maxWidth: "160px", backgroundColor: this.colorOrden("monto") }} onClick={() => { this.setOrden("monto") }}>MONTO {this.flechas("monto")}</th>
            <th style={{ minWidth: "100px", maxWidth: "100px" }}>ESTATUS</th>
            <th style={{ minWidth: "135px", maxWidth: "135px" }}>ACCIONES</th>
        </tr>
    }
    Row(c: any) {
        return <></>;
    }
    setpage(page: number) {
        this.setState({ page })
    }
    setItens(itens: Array<any>) {
        this.setState({ itens })
    }
    setStatus(e: React.ChangeEvent<HTMLSelectElement>) {
        let value = e.currentTarget.value as string;
        let r = parseInt(value);
        this.setState({ estatus: r });
    }
    setFechaInicio(e: React.ChangeEvent<HTMLInputElement>) {
        let value = e.currentTarget.value as string;
        this.setState({ fecha_ini: value });
    }
    setFechaFin(e: React.ChangeEvent<HTMLInputElement>) {
        let value = e.currentTarget.value as string;
        this.setState({ fecha_fin: value });
    }
    limpiarFechas() {
        this.setState({ fecha_ini: 'dd/mm/aaaa', fecha_fin: 'dd/mm/aaaa' });
    }
    addProducto(p: Producto) {
        this.setState({ productos: [...this.state.productos, p] })
    }
    removeProducto(cod: String) {
        this.setState({
            productos: this.state.productos.filter(p => {
                if (p.codigo === cod) return false;
                return true;
            })
        })
    }
    componentDidMount() {
        this.actualizar();
    }
    actualizar() {
        this.set();
        setTimeout(() => {
            this.actualizar()
        }, 36000);
    }
    set() {

    }
}