import { GeneralFila } from "../Modelos/General"
import { Producto } from "../Modelos/Productos";
export type filaLabel = "codigo" | "fecha" | "nombre" | "telefono" | "nota" | "tasa" | "monto" | "usuario" | "status";

export function ordenarPor<p>(itens: any[], orden: string, label: p | 'fecha' | 'codigo') {
    return itens.sort((a, b) => {
        if (label === 'fecha') {
            let fa = datetonomber(a.fecha), fb = datetonomber(b.fecha);
            if (orden === "desc")
                return (fb < fa ? - 1 : (fb > fa ? 1 : 0));
            else
                return (fa < fb ? - 1 : (fa > fb ? 1 : 0));
        } else if (isNaN(b[label])) {
            if (orden === "desc")
                return (a[label] < b[label] ? - 1 : (a[label] > b[label] ? 1 : 0));
            else
                return (b[label] < a[label] ? - 1 : (b[label] > a[label] ? 1 : 0));
        } else {
            if (orden === "desc")
                return (b[label] - a[label]);
            else
                return (a[label] - b[label]);
        }
    })
}
export function busqueda<p>(itens: any[], encontar: string, bucarLabel: p[]) {
    if (encontar !== "") {
        return itens.filter(iten => {
            let en = encontar.split(" "), pasa = false;
            bucarLabel.forEach(label => {
                let considencias = 0;
                en.forEach(e => {
                    if (isNaN(iten[label])) {
                        if (iten[label].toUpperCase().indexOf(e) > -1) { considencias++ }
                    } else {
                        if (zfill(iten[label]).indexOf(e) > -1) { considencias++ }
                    }
                })
                if (en.length === considencias) {
                    pasa = true
                }
            })
            return pasa
        })
    }
    return itens;
}
export function busquedaFechas(itens: GeneralFila[], fini: string, ffin: string) {
    if ((ffin !== 'dd/mm/aaaa') && (fini !== 'dd/mm/aaaa')) {
        var ini = datetonomber(fini);
        var fin = datetonomber(ffin);
        return itens.filter((iten) => {
            var fecha = datetonomber(iten.fecha);
            if ((ini <= fecha) && (fin >= fecha)) {
                return true;
            }
            return false;
        });
    }
    return itens;
}
export function filtro(itens: any[], value: number, Label: filaLabel) {
    if (value !== -1)
        return itens.filter(iten => {
            return iten[Label] === value;
        })
    return itens;
}
export function filtrarProducto(itens: GeneralFila[], values: Producto[]) {
    return itens.filter(iten => {
        let pasa: string[] = [];
        values.forEach(cod => {
            iten.detalles.forEach(e => {
                if (cod.codigo === e) pasa.push(e);
            })
        })
        if (pasa.length === values.length || values.length === 0) return true;
        return false;
    })
}
export function numberFormat(number: number) {
    return (number
        .toFixed(2) // always two decimal digits
        .replace('.', ',') // replace decimal point character with ,
        .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.')) // use . as a separator
}
export function zfill(number: number, width = 6) {
    var numberOutput = Math.abs(number) /* Valor absoluto del número */
    var length = number.toString().length /* Largo del número */
    var zero = "0" /* String de cero */

    if (width <= length) {
        if (number < 0) {
            return ("-" + numberOutput.toString())
        } else {
            return numberOutput.toString()
        }
    } else {
        if (number < 0) {
            return ("-" + (zero.repeat(width - length)) + numberOutput.toString())
        } else {
            return ((zero.repeat(width - length)) + numberOutput.toString())
        }
    }
}
export function datetonomber(fecha: string) {
    var date = new Date(fecha);
    var año = date.getFullYear();
    var mes = date.getMonth() + 1;
    var dia = date.getDate() + 1;
    año *= 10000;
    mes *= 100;
    return año + mes + dia;
};
export function formatoFecha(fecha: string) {
    var date = new Date(fecha);
    var año = date.getFullYear();
    var mes = date.getMonth() + 1;
    var dia = date.getDate() + 1;
    return dia + '/' + mes + '/' + año;
};
export function formatoHora(fecha: string) {
    let date = new Date(fecha);
    let hora = date.getHours(),
        minuto = date.getMinutes(),
        segundo = date.getSeconds();
    return hora + ":" + minuto + ":" + segundo;
};
export function FormDataFotmat(o: any) {
    const formData = new FormData();
    Object.keys(o).forEach(key => {
        switch (typeof o[key]) {
            case 'object':
                formData.append(key, JSON.stringify(o[key]));
                break;
            default:
                formData.append(key, o[key]);
        }
    });
    return formData;
};

export function post(url = '', obj = {}, success = (d: any) => { }, error = (d: any) => { }) {
    var xhr = new XMLHttpRequest();
    xhr.responseType = 'json';
    xhr.open("POST", url);
    xhr.onreadystatechange = (e) => {
        if (xhr.readyState !== 4) {
            return;
        }
        if (xhr.status === 200) {
            success(xhr.response);
        } else {
            error(xhr);
        }
    };
    xhr.send(FormDataFotmat(obj));
};