import React from "react";
import { Button, Modal, Pagination, Row, Table } from "react-bootstrap";
import { busqueda, ordenarPor } from "./Utiles";
interface State<t, l> {
    itens: t[] | 'carro'
    buscar: string
    page: number
    ordenar: 'codigo' | l
}
interface Props {
    open: boolean,
    close: () => void
}
export default class ModalBusqueda<p, t, l> extends React.Component<Props & p, State<t, l>>{
    placeholder = "";
    mostrar = 8
    buscarLabel: l[] = []
    estatus: { label: string, value: number }[] = []
    orden: string = "desc";
    length = 0;
    constructor(p: any) {
        super(p)
        this.setState = this.setState.bind(this);
        this.setBuscar = this.setBuscar.bind(this);
        this.state = { itens: "carro", buscar: '', page: 1, ordenar: 'codigo' };
    }
    body() {
        var page = this.state.page - 1
        var init = page * this.mostrar
        var fin = init + this.mostrar
        var itens = this.filtros().filter((iten, index) => {
            if (index >= init && index < fin) {
                return true
            }
            return false
        })
        var pts = itens.map((iten) => {
            return this.Row(iten)
        })
        return pts
    }
    paginacion() {
        if (this.state.itens.length === 0)
            return <></>
        var total_page = Math.ceil(this.state.itens.length / this.mostrar)
        var page = this.state.page
        var buttons = []
        if (page === 1) {
            buttons.push(<Pagination.Item key="active1" active>1</Pagination.Item>)
        } else {
            buttons.push(<Pagination.Item key="p1" onClick={this.setpage.bind(this, 1)}>1</Pagination.Item>)
        }
        for (var i = 3; i > 0; i--) {
            if ((page - i) > 1) {
                if (i === 3) {
                    buttons.push(<Pagination.Ellipsis key="Ellipsis1" />)
                } else {
                    buttons.push(<Pagination.Item key={"p" + (page - i)} onClick={this.setpage.bind(this, page - i)}>{page - i}</Pagination.Item>)
                }
            }
        }
        if (total_page > page && page > 1) {
            buttons.push(<Pagination.Item active>{page}</Pagination.Item>)
        }
        for (i = 1; i < 4; i++) {
            if ((page + i) < total_page) {
                if (i === 3) {
                    buttons.push(<Pagination.Ellipsis key="Ellipsis2" />)
                } else {
                    buttons.push(<Pagination.Item key={"p" + (page + i)} onClick={this.setpage.bind(this, page + i)}>{page + i}</Pagination.Item>)
                }
            }
        }
        if (page === total_page) {
            if (1 !== total_page) {
                buttons.push(<Pagination.Item key={"active" + total_page} active>{total_page}</Pagination.Item>)
            }
        } else {
            buttons.push(<Pagination.Item key={"p" + total_page} onClick={this.setpage.bind(this, total_page)}>{total_page}</Pagination.Item>)
        }
        return buttons
    }
    pagination() {
        return <Pagination style={{ alignContent: "right" }}>{this.paginacion()}</Pagination>
    }
    setBuscar(e: React.KeyboardEvent<HTMLInputElement>) {
        let { value } = e.currentTarget
        this.setState({ buscar: value.toUpperCase() });
        this.setpage(1)
    }
    setOrden(label: l) {
        if (this.state.ordenar === label) {
            if (this.orden === "asc")
                this.orden = "desc";
            else
                this.orden = "asc";
            this.forceUpdate();
        } else {
            this.orden = "desc";
            this.setState({ ordenar: label })
        }
    }
    flechas(label: l) {
        if (this.state.ordenar === label) {
            if (this.orden === "desc") {
                return <i className="fa fa-arrow-down pull-right" />
            } else {
                return <i className="fa fa-arrow-up pull-right" />
            }
        }
        return <></>
    }
    filtros() {
        let { itens, buscar, ordenar } = this.state;
        if (itens !== 'carro') {
            itens = busqueda(itens, buscar, this.buscarLabel);
            itens = ordenarPor<l>(itens, this.orden, ordenar);
            return itens;
        }
        return [];
    }
    render() {
        return <Modal show={this.props.open} onHide={this.props.close} bsSize="lg">
            <Modal.Header>
                {this.title()}
            </Modal.Header>
            <Modal.Body>
                <div style={{ padding: "10px 10px 10px 10px" }}>
                    {this.renderBusqueda()}
                    <Row>
                        <Table>
                            <thead>
                                {this.Header()}
                            </thead>
                            <tbody>
                                {this.body()}
                            </tbody>
                        </Table>
                    </Row>
                </div>
            </Modal.Body>
        </Modal>
    }
    title() {
        return <></>;
    }
    renderBusqueda() {
        return <Row>
            <div className="input-group col-md-12">
                <input style={{ position: 'relative', width: "50%" }} onKeyUp={this.setBuscar.bind(this)} name="buscar" placeholder={this.placeholder} className="form-control" />
                <div className="input-group-append">
                    <Button style={{ width: '100px', position: 'relative', borderTopLeftRadius: 0, borderBottomLeftRadius: 0 }} bsStyle="primary">
                        <span className="glyphicon glyphicon-search"></span> Buscar
                    </Button>
                </div>
            </div>
        </Row>
    }
    Header() {
        return <tr>
        </tr>
    }
    Row(c: any) {
        return <></>;
    }
    setpage(page: number) {
        this.setState({ page })
    }
    setItens(itens: Array<any>) {
        this.setState({ itens })
    }
}