import React from 'react';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom"
import ListCotizaciones from './App/Cotizacion';
import ListFactura from './App/Facturas';
import ListNotas from './App/Nota';
import ListOrdenes from './App/Orden';
import ListConpra from './App/Compra';

function App() {
  return (<div className="container-fluid">
    <style dangerouslySetInnerHTML={{ __html: ".modal-detalle{width:90%}" }}></style>
    <Router>
      <Switch>
        <Route path="/ver_cotizaciones" component={ListCotizaciones} />
        <Route path="/ver_ordenes" component={ListOrdenes} />
        <Route path="/ver_facturas" component={ListFactura} />
        <Route path="/ver_notas" component={ListNotas} />
        <Route path="/ver_compras" component={ListConpra} />
      </Switch>
    </Router>
  </div>);
}

export default App;
