import React from 'react'
import { Modal, Row, Table } from 'react-bootstrap';
import { GeneralData, generalNull } from '../Modelos/General';
import { numberFormat } from './Utiles';
interface Props {
    id: number
    open: boolean,
    close: () => void,
    titulo: string
    tipo: 'Cotizacion' | 'Orden' | 'Nota' | 'Factura' | 'Compra'
}
export default class ModalDetalle extends React.Component<Props, GeneralData> {
    constructor(p: Props) {
        super(p);
        this.state = generalNull;
    }
    render() {
        let { detalles } = this.state;
        let suma = 0;
        if (detalles === undefined)
            return <></>
        return <Modal dialogClassName="modal-detalle" animation show={this.props.open} onHide={this.props.close}>
            <Modal.Header closeButton>
                <h6 className="m-0 font-weight-bold text-primary mr-auto align-self-center">
                    <span><b>{this.props.titulo}</b></span>
                </h6>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Table>
                        <thead>
                            <tr>
                                <th style={{ width: "100px" }}>CODE</th>
                                <th>DESCRIPCION</th>
                                <th style={{ width: "40px", textAlign: "right" }}>CANT</th>
                                <th style={{ width: "80px", textAlign: "right" }}>PRE/UNT</th>
                                <th style={{ width: "150px", textAlign: "right" }}>PRECIO</th>
                                <th>NOTA</th>
                            </tr>
                        </thead>
                        <tbody>
                            {detalles.map((s) => {
                                suma += s.precio * s.unidades
                                return <tr>
                                    <td style={{ width: "100px" }}>{s.codigo}</td>
                                    <td>{s.descripcion}</td>
                                    <td style={{ width: "40px" }}>{numberFormat(s.unidades)}</td>
                                    <td style={{ width: "80px", textAlign: "right" }}>{numberFormat(s.precio)}</td>
                                    <td style={{ width: "150px", textAlign: "right" }}>{numberFormat(s.precio * s.unidades)}</td>
                                    <td>{s.comentario}</td>
                                </tr>
                            })}
                            <tr>
                                <td colSpan={3}></td>
                                <td style={{ width: "80px", textAlign: "right" }}>total:</td>
                                <td style={{ width: "150px", textAlign: "right" }}>{numberFormat(suma)}</td>
                            </tr>
                        </tbody>
                    </Table>
                </Row>
            </Modal.Body>
        </Modal>
    }
    dialog() {
        return <Modal.Dialog style={{ width: "90%" }} />
    }
    componentDidMount() {
        this.set();
    }
    set() {
        fetch('/api/' + this.props.tipo + '/detalles/' + this.props.id)
            .then(response => response.json())
            .then(data => {
                this.setState(data);
                this.forceUpdate();
            });
    }
}