import React from 'react'
import { UsuarioFila } from '../../Modelos/Usuario';

interface Props {
    change: (r: number) => void
    defaultValue: number
}
interface State {
    usuarios: UsuarioFila[] | 'carro'
}
export default class UsuarioSelector extends React.Component<Props, State>{
    constructor(p: Props) {
        super(p);
        this.state = { usuarios: 'carro' };
    }
    setUsers(c: UsuarioFila[]) {
        this.setState({ usuarios: c })
    }
    render() {
        return <select onChange={this.setRol.bind(this)} className="form-control" defaultValue={this.props.defaultValue}>
            <option value={-1}>TODOS LOS USUARIOS</option>
            {(this.state.usuarios !== 'carro') ? this.state.usuarios.map(c => {
                return <option key={"user" + c.codigo} value={c.codigo}>{c.nombre}</option>
            }) : <></>}
        </select>
    }
    componentDidMount() {
        this.set();
    }
    setRol(Rol: React.ChangeEvent<HTMLSelectElement>) {
        let value = Rol.currentTarget.value as string
        let r = parseInt(value)
        this.props.change(r)
    }
    set() {
        fetch('/api/Usuario/lista')
            .then(response => response.json())
            .then(data => {
                this.setUsers(data);
            });
    }
}